import utlis from '../../static/js/utlis.js'
export default {
	data() {
		return {
			top_active: '',
			balance: '0.00元'
		}
	},
	onShow() {
		this.getUserInfo()
	},
	methods: {
		showMywalletDetail(e) {
			uni.navigateTo({
				url: '/pages/myWalletDetail/myWalletDetail?top_active=' + e.target.dataset.top_active + '&top_data=' +
					e.target.dataset.top_data
			})
		},
		showRecharge(e) {
			uni.navigateTo({
				url: '/pages/recharge/recharge'
			})
		},
		//获取用户信息
		getUserInfo(e) {
			let self = this;
			let data = {
				custName: uni.getStorageSync('custName')
			}
			utlis.http("POST", 'appCustCenter', data, function(res) {
				if (res.status == "success") {
					uni.setStorageSync("custName", res.custName);
					self.phone = res.custName;
					self.balance = res.balance;
				} else {

				}
			})
		}
	}
}
